import React from 'react'

export default class extends React.Component {
	static async getInitialProps({req}){
		// console.log(req)
		const userAgent = req ? req.headers['user-agent']: navigator.userAgent
		return {userAgent}
	}
	render(){
		return(
		<div>
			Hello World {this.props.userAgent}
		</div>
		)
	}
}